import React from "react"
import { Card, CardBody, Input, Button, } from "reactstrap"
import { AgGridReact } from "ag-grid-react"
import { ContextLayout } from "../../utility/context/Layout"
// import { ChevronDown } from "react-feather"
import axios from "axios";
import {connect} from "react-redux";
import Customize from "./CustomizeDeals"
import Chart from "react-apexcharts"

import Breadcrumbs from "../../components/@vuexy/breadCrumbs/BreadCrumb"

import "../../assets/scss/plugins/tables/_agGridStyleOverride.scss"
import "../../assets/scss/plugins/tables/_customAgGridStyleOverride.scss"
import "../../assets/scss/pages/app-email.scss"

class DeskManager extends React.Component {
    state = {
        rowData: null,
        pinnedTopRowData: [],
        pinnedBottomRowData: [],
        paginationPageSize: 20,
        currenPageSize: "",
        getPageSize: "",
        defaultColDef: {},
        columnDefs: [
            {
                headerName: "Desk Manager",
                field: "desk_manager",
                filter: true,
                width: 180,
                cellStyle: {color: 'black', 'font-size': "14px"}
            },
            {
                headerName: "Sales",
                field: "sales",
                filter: true,
                width: 180,
                cellStyle: {color: 'black', 'font-size': "14px"}
            },
            {
                headerName: "Payable Gross",
                field: "payable_gross",
                filter: true,
                width: 180,
                cellStyle: {color: 'black', 'font-size': "14px"}
            },
            {
                headerName: "Front",
                field: "front",
                filter: true,
                width: 180,
                cellStyle: {color: 'black', 'font-size': "14px"}
            },
            {
                headerName: "Warranty Pen%",
                field: "warranty_pen",
                filter: true,
                width: 180,
                cellStyle: {color: 'black', 'font-size': "14px"}
            },
            {
                headerName: "Warranty Gross",
                field: "warranty_gross",
                filter: true,
                width: 180,
                cellStyle: {color: 'black', 'font-size': "14px"}
            },
            {
                headerName: "Gap Pen%",
                field: "gap_pen",
                filter: true,
                width: 180,
                cellStyle: {color: 'black', 'font-size': "14px"}
            },
            {
                headerName: "Gap Gross",
                field: "gap_gross",
                filter: true,
                width: 180,
                cellStyle: {color: 'black', 'font-size': "14px"}
            },
            {
                headerName: "Miscfin Pen%",
                field: "miscfin_pen",
                filter: true,
                width: 180,
                cellStyle: {color: 'black', 'font-size': "14px"}
            },
            {
                headerName: "Miscfin Gross",
                field: "miscfin_gross",
                filter: true,
                width: 180,
                cellStyle: {color: 'black', 'font-size': "14px"}
            },
            {
                headerName: "Back End",
                field: "back_end",
                filter: true,
                width: 180,
                cellStyle: {color: 'black', 'font-size': "14px"}
            },
            {
                headerName: "Total Gross",
                field: "total_gross",
                filter: true,
                width: 180,
                cellStyle: {color: 'black', 'font-size': "14px"}
            },
        ],
        composeMailStatus: false,
        options: {
            colors: this.props.themeColors,
            plotOptions: {
                bar: {
                    horizontal: true
                }
            },
            dataLabels: {
                enabled: false
            },
            xaxis: {
                categories: [
                    "Adam",
                    "Miguel",
                    "Jake J",
                    "Tyler",
                    "House",
                ],
                tickAmount: 5
            }
        },
        series: [
            {
              data: [540, 470, 448, 430, 400]
            }
        ]
    }

    componentDidMount() {
        axios.get("/api/aggrid/data").then(response => {
        // let rowData = response.data.data // replace for product
            let tempdata = {
                desk_manager: "Jake J",
                sales: "14",
                payable_gross: "$1,582.00",
                front: "$3,532.00",
                warranty_pen: "25.0%",
                warranty_gross: "$5,910.00",
                gap_pen: "44.4%",
                gap_gross: "$1,849.00",
                miscfin_pen: "42.9%",
                miscfin_gross: "$3,186.00",
                back_end: "$16,500.00",
                total_gross: "$42,031.00"
            };

            let colWidth = window.innerWidth;
            let columnDefs = this.state.columnDefs
            
            for (let i = 0; i < this.state.columnDefs.length; i++) {
                columnDefs[i].width = (colWidth-(colWidth/5)) / this.state.columnDefs.length;
                columnDefs[i].minWidth = 120;
            }

            let rowData = [];
            for (let i = 0; i < 20; i++) {
                rowData.push(Object.assign({}, tempdata));
            }

            JSON.stringify(rowData)
            this.setState({
                rowData,
                columnDefs
            });
        })
    }

    UNSAFE_componentWillMount () {
        this.initTable(this.props);
    }

    UNSAFE_componentWillReceiveProps (nextprops) {
        this.initTable(nextprops);
    }

    initTable(props) {
        let pinnedBottomRowData = [];
        pinnedBottomRowData.push(
            {
                desk_manager: "Total",
                sales: "46",
                payable_gross: "$1,582.00",
                front: "$3,532.00",
                warranty_pen: "25.0%",
                warranty_gross: "$5,910.00",
                gap_pen: "44.4%",
                gap_gross: "$1,849.00",
                miscfin_pen: "42.9%",
                miscfin_gross: "$3,186.00",
                back_end: "$16,500.00",
                total_gross: "$42,031.00"
            }
        );
        pinnedBottomRowData.push(
            {
                desk_manager: "Average",
                payable_gross: "$1,582.00",
                front: "$3,532.00",
                warranty_gross: "$5,910.00",
                gap_gross: "$1,849.00",
                miscfin_gross: "$3,186.00",
                back_end: "$16,500.00",
                total_gross: "$42,031.00"
            }
        );

        this.setState({
            pinnedBottomRowData,
            composeMailStatus: false
        });
    }

  onGridReady = params => {
    this.gridApi = params.api
    this.gridColumnApi = params.columnApi
    this.setState({
      currenPageSize: this.gridApi.paginationGetCurrentPage() + 1,
      getPageSize: this.gridApi.paginationGetPageSize(),
      totalPages: this.gridApi.paginationGetTotalPages()
    })
  }

  updateSearchQuery = val => {
    this.gridApi.setQuickFilter(val)
  }

  filterSize = val => {
    if (this.gridApi) {
      this.gridApi.paginationSetPageSize(Number(val))
      this.setState({
        currenPageSize: val,
        getPageSize: val
      })
    }
  }
  handleCustomize() {
    this.handleComposeSidebar("open")
    this.onSetSidebarOpen(false)
  }
  handleComposeSidebar = status => {
    if (status === "open") {
      this.setState({
        composeMailStatus: true
      })
    } else {
      this.setState({
        composeMailStatus: false
      })
    }
  }

  onSetSidebarOpen = open => {
    this.setState({ sidebarOpen: open })
  }
  handleMainAndComposeSidebar = () => {
    this.handleComposeSidebar("close")
    this.onSetSidebarOpen(false)
  }

  render() {
    const { rowData, defaultColDef, pinnedBottomRowData } = this.state

    return (
    <React.Fragment>
        <Breadcrumbs breadCrumbTitle="Desk Manager" breadCrumbParent="Reports" breadCrumbActive="Desk Manager"/>
        <Card onClick={this.handleMainAndComposeSidebar}>
            <CardBody>
                <div className="d-flex flex-wrap justify-content-end align-items-center">
                    <div className="mb-1"></div>
                    <div className="d-flex flex-wrap justify-content-between mb-1">
                        <div className="table-input mr-1">
                            <Input
                                placeholder="search..."
                                onChange={e => this.updateSearchQuery(e.target.value)}
                                value={this.state.value}
                            />
                        </div>
                        <div className="customize-btn">
                            <Button.Ripple
                                block
                                color="primary"
                                onClick={() => this.handleCustomize()}
                            >
                            Customize
                            </Button.Ripple>
                        </div>
                        <div className="export-btn">
                            <Button.Ripple
                                color="primary"
                                onClick={() => this.gridApi.exportDataAsCsv()}
                            >
                            Export as CSV
                            </Button.Ripple>
                        </div>
                    </div>
                </div>
                <Chart
                    options={this.state.options}
                    series={this.state.series}
                    type="bar"
                    height={350}
                />
                <div className="d-flex flex-wrap mb-1">
                    <div className="mr-1">
                        <Button.Ripple
                            block
                            color="primary"
                        >
                            Total Sales
                        </Button.Ripple>
                    </div>
                    <div className="mr-1">
                        <Button.Ripple
                            block
                            color="primary"
                        >
                            Total Gross
                        </Button.Ripple>
                    </div>
                    <div className="mr-1">
                        <Button.Ripple
                            block
                            color="primary"
                        >
                            Average Gross
                        </Button.Ripple>
                    </div>
                    <div className="mr-1">
                        <Button.Ripple
                            block
                            color="primary"
                        >
                            Total Back End
                        </Button.Ripple>
                    </div>
                    <div className="mr-1">
                        <Button.Ripple
                            block
                            color="primary"
                        >
                            Average Back End
                        </Button.Ripple>
                    </div>
                </div>
            </CardBody>
        </Card>
        <div className='email-application'>
            <Customize 
                handleComposeSidebar={this.handleComposeSidebar}
                currentStatus={this.state.composeMailStatus}
            />
            <Card className="agGrid-card" style={{height: "100% !important", paddingBottom: "2%"}}>
                <CardBody className="py-0" style={{height: "100% !important"}}>
                {
                this.state.rowData === null ? null : (
                    <div className="ag-theme-material w-100 my-2 ag-grid-table" style={{height: "100% !important", width: "100% !important"}}>
                        <ContextLayout.Consumer>
                            {context => (
                            <AgGridReact
                                gridOptions={{}}
                                rowSelection="multiple"
                                defaultColDef={defaultColDef}
                                columnDefs={this.state.columnDefs}
                                rowData={rowData}
                                onGridReady={this.onGridReady}
                                // colResizeDefault={"shift"}
                                // animateRows={true}
                                floatingFilter={true}
                                pagination={true}
                                // paginationPageSize={this.state.paginationPageSize}
                                // pivotPanelShow="always"
                                enableRtl={context.state.direction === "rtl"}
                                
                                pinnedBottomRowData={pinnedBottomRowData}
                                rowHeight="40"
                            />
                            )}
                        </ContextLayout.Consumer>
                    </div>
                )}
                </CardBody>
            </Card>
        </div>
    </React.Fragment>
    )
  }
}

const mapStateToProps = state => {
    return {
        app: state.calender
    }
}

export default connect(mapStateToProps, {})(DeskManager);
